.preloader {
  font-family: 'Lato', sans-serif;
  position: relative;
  color: #212121;
  font-size: 50px;
  letter-spacing: 5px;
  text-transform: uppercase;
  border-bottom: 5px solid #212121;
}

.preloader:before {
  position: absolute;
  top: 0;
  left: 0;
  content: 'Loading...';
  color: #00ffff;
  border-bottom: 5px solid #00ffff;
  overflow: hidden;
  animation: loading 4s infinite linear;
}

@keyframes loading {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}